<template>
	<div id="eidt_header">
		<div class="header-bar">
			<img class="lt-arrow" src="../../assets/arrow.png" @click="$router.go(-1)"/>
			<span class="title">{{headerTitle}}</span>
			<slot name="edit"></slot>
		</div>
	</div>
</template>

<script>
	export default {
		props:['headerTitle'],
		methods: {
			
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";

	#eidt_header{
		height: 1.17rem;
		width:100%;
		.header-bar{
			@include headerStyle;
			.lt-arrow{
				/*@include lt-arrow;*/
				display: inline-block;
				@include lt-arrow;
			}
			.title{
				font-size: 0.48rem;
			}			
			.edit{
				position: absolute;
				right: 0.4rem;
				font-size: 0.4rem;
			}
		}
	}
</style>


